.card {
  border: 1px solid #ccc;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(58, 78, 99, 0.793);
}

.card img {
  height: 100%;
}

.icon-section {
  height: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.icons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2px;
  border-radius: 5px;
  padding: 5px;
  max-width: calc(2 * var(--icon-size) + var(--gap-size)); /* 2 icon + 1 gap */
}

.icon {
  flex: 1 0 auto;
  width: var(--icon-size);
  height: var(--icon-size);
}

.select-style {
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 12px;
  font-weight: bold;
  color: #333333c1;
}

.car-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 5%;
}

.Car-lst {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.filter-menu {
  top: 10%;
  background-color: rgba(237, 237, 243, 0.949);
  position: absolute;
  width: 100%;
  box-shadow: 0 2px 4px rgba(208, 167, 167, 0.942);
  z-index: 200;
  transition: top 0.3s ease;
  display: flex; /* Eklenen zellik */
  justify-content: center; /* Eklenen zellik */
}

.filter-item {
  margin-bottom: 10px;
  width: calc(23.33% - 10px);
  /* float: right; Bu zellii kaldrn */
}


.filter-item label {
  display: inline-block;
  width: 100%; /* stediiniz genilii buraya yazn */
}

.filter-item select,
.filter-item input {
  width: calc(100% - 16px); /* kk bir dzeltme */
  padding: 6px; /* daha kk padding */
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 10px; /* daha kk font boyutu */
}

.filter-btn {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.filter-btn:hover {
  background-color: #0056b3;
}

@media screen and (max-width: 750px) {
  .filter-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .filter-item {
    width: 50%;
    box-sizing: border-box;
    padding: 0 10px; /* stee bal: eler arasnda biraz boluk brakr */
  }
  .car-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    margin-top: 250px;
  }
  
}


/*campain*/
.campaign-cars-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  font-family: Arial, sans-serif;
  color: #333;
}

.campaign-cars-page h2 {
  color: #007bff;
}

.campaign-cars-page p {
  color: #666;
}

.campaign-cars-page img {
  max-width: 100%;
  height: auto;
}

.campaign-cars-page input[type="date"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.campaign-cars-page button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.campaign-cars-page button:hover {
  background-color: #0056b3;
}